/* @flow */

import React from 'react';
import styled from 'styled-components';
import Header from '../../components/Header'
import Navigation from '../../components/Navigation'
import Footer from '../../components/Footer'
import ContentWrapper from '../../components/ContentWrapper';
import {Parallax} from 'react-parallax';
import YouTube from 'react-youtube';
import PayPalDonate from '../../components/PayPalDonate';
import PayPalBuy from '../../components/PayPalBuy';
import {Title, Para, TextWrapper} from '../../components/common';
import {YouTubeReikiPlaylistOptions} from '../common';

const Container = styled.div`
`;


type Props = {};

class FreeAttunements extends React.Component<Props> {
  render() {
    return (
      <Container>
        <Navigation/>
        <ContentWrapper>
          <TextWrapper>
            <Title>Free Reiki Attunement and Diploma Certificate for level 1, 2 and 3</Title>
            <Para>Free distant attunement video watch now and be attuned to all 3 levels of reiki at once.</Para>
            {this.createYouTube()}
            <Para>
              To get your free diploma certificate please make a donation below. It is needed to maintain the costs
              of this website.
            </Para>
            <PayPalDonate/>
            <Para>Start your own reiki school online, get all you need by buying with the button below.</Para>
            {/*@todo: add link to reiki products*/}
            <PayPalBuy/>
          </TextWrapper>
        </ContentWrapper>
        <Footer/>
      </Container>
    );
  }

  createYouTube() {
    const props = {
      videoId: `ZNP0IUKMYyk`,
      ...YouTubeReikiPlaylistOptions,
    };
    return <YouTube {...props}/>
  }
}

export default FreeAttunements;



