/* @flow */

import styled from 'styled-components';
import Link from './Link';

export const Title = styled.h1`
  color: #362c2a;
  margin-top: 20px;
`;

export const Para = styled.p`
  color: #362c2a;
  line-height: 1.4;
  margin: 5px;
`;

export const TextWrapper = styled.div`
  max-width: 640px;
  margin: 0 auto;
  overflow: hidden;
  margin-bottom: 10px;
`;

export const Li = styled.li`
  color: #362c2a;
  line-height: 1.4;
  margin: 5px;
`;

export const ContentCTA = styled(Link)`
  display: inline-block;
  text-align: center;
  background: #e72f34;
  background: linear-gradient(to bottom, rgba(235,73,78,1) 0%, rgba(231,47,53,1) 100%);
  padding: 10px;
  border-radius: 4px;
  color: white;
  text-decoration: none;
  font-weight: bold;
  font-style: italic;
  font-variant: small-caps;
  font-size: 22px;
  box-shadow: 0px 6px #9e1e21;
  text-align: center;
  transition: 0.3s all;
  margin-bottom: 16px;
  
  &:hover {
    box-shadow: 0px 3px #9e1e21;
    transform: translate(0px, 3px);
  }
  
  &:active {
    box-shadow: 0px 1px #9e1e21;
    transform: translate(0px, 5px);
  }
`;

export const ContentSecCTA = ContentCTA.extend`
  font-size: 16px;
  font-weight: normal;
  box-shadow: 0px 4px #9e1e21;
  margin-bottom: 14px;
  padding: 7px;
  
  &:hover {
    box-shadow: 0px 2px #9e1e21;
    transform: translate(0px, 2px);
  }
  
  &:active {
    box-shadow: 0px 1px #9e1e21;
    transform: translate(0px, 3px);
  }
`;


export const CenterInline = styled.div`
  text-align: center;
`;



